.card{
    border-radius: 9px;
    overflow: hidden;
    background: var(--colorCard);
    text-align: center;
    padding:2em 0;
    position: relative;
    color: var(--textCard);

} 
.total-followers{
    color:  var(--textLight);
    margin: .5em 0; /*5 em hacia arriba y acia abajo y 0 a los costados*/
    font-weight: bold;
    border-bottom:  1px solid gray;
    padding-bottom: 1.5em;
    font-size: 1em;
}
.top-cards{
    margin-top: -50px;
}
.card:before{ /*pseudoelemto */
   content: "";
   height: 4px;
   display: block;
   left: 0;
   right: 0;
   top: 0;
   position: absolute;
}
.card-title{
   display: flex;
   align-items: center;
   justify-content: center;
   font-size: .8em;
   color: rgb(90, 88, 88);
   font-weight: bold;
}
.card-title img{
   margin-right: 5px;
}
.card-followers-number{
    font-size: 50px;
    font-weight: 700;
    color: var(--title-number);
}
.card-followers-title{
    display: block;
    text-transform: uppercase;
    color: gray;
    letter-spacing: 5px;
    font-size: .75em;
}
.card-today{
    margin-top: 20px;
    color: var(--line-green);
    font-size: .7em;
    font-weight: bold;
    display: flex;
    justify-content: center;
    align-items: center;
}
.card-today img{
    margin-right: 5px;
}
.card.facebook::before{
    background: var(--facebook);
}
.card.twitter::before{
    background: var(--twitter);

}

.card.instagram::before{
    background-image:linear-gradient(to right,var(--instagram-1) 0%,var(--intagram-2) 100%);

}

.card.youtube::before{
    background: var(--youtube);

}
