
.card-small{
    
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-row-gap: 1em;
    background: var(--colorCard);
    color: var(--textCard);
    border-radius: 8px;
    padding:1.7em;
}
.card-small p{
    margin: 0;
}
.card-small-views{
font-weight: bold;
font-size: .9em;
color: var(--gray);
}
.card-small-number{
    font-weight: bold;
    font-size: 2.7em;
    line-height: .9;
    color: var(--title-number);
}
.card-small-icon{
    text-align: right;
}
.card-small-porcentage{
    text-align: right;
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
    color: var(--line-green); 
    font-size: .8em;
    font-weight: bold;

}
.card-small-porcentage:nth-child(2n+1) {
    color: var(--gright-red);
}


.card-small-porcentage span{
    display: flex;
    align-items: center;
}
.card-small-porcentage img{
  margin-right: 5px;
}

