
.is-light-mode{
    --title-number: black;
    --colorCardLight: #f0f3fa;
    --colorCard: #f0f3fa;
    --gray:gray;
    --base: white;
    --textLight: rgb(90, 88, 88);
    --textCard: black;
    --switchSpinner: white;
    --switch: #5f616d;

}
.is-dark-mode{
    --title-number: white;
    --colorCard: #252b43;
    --textLight: white;
    --base: #1d2029;
    --switchSpinner: #282843;
    --switch: linear-gradient(to right,#4796d2 0%,#37ce8f 100%);
    
}

@media (prefers-color-scheme: dark){

    :root{
        --colorCard: #252b43;
        --textLight: white;
        --base: #1d2029;
        --switchSpinner: #282843;
        --switch: linear-gradient(to right,#4796d2 0%,#37ce8f 100%);
    
    }
 

}