.checkbox{
    display: none;
}

.checkbox:checked ~ .switch:before{
    transform: translateX(0);

}
.switch{

    height: 40px;
    display: block;
    background: var(--switch);
    border-radius: 20px;
    width: 80px;
    padding:5px;
    box-sizing: border-box;
    cursor:pointer;
    user-select:none;
}

.switch::before{
    content: "";
    display:block;
    width: 30px;
    height: 30px;
    background: var(--switchSpinner);
    border-radius: 50px;
    transform: translateX(40px);
    transition: .3s;
    will-change: transform;
}
.dark-mode{
    display:flex;
    justify-content: space-between;
    padding-top: 1em;
    font-weight: bold;
}