body{
    margin: 0;
    font-family: 'Inter', sans-serif;
    color:  var(--textLight);
    background: var(--base);
    }
.overview{
        margin-bottom: 3em;
}
h2{
        color: gray;
        margin:0;
        margin-top: 1.5em;
        margin-bottom: 1em;
    }
     
:root{ /*psudoclase */
        --line-green: hsl(163,72%,41%);
        --gright-red: hsl(356,69%,56%);
        --facebook:#1976D2 ; 
        --twitter: hsl(203,89%,53% );
        --instagram-1: hsl(37,97%,70%);
        --intagram-2: hsl(329, 70%,58%);
        --youtube: hsl(348,97%,39%);
        --colorCardLight: #f0f3fa;
        --colorCard: #f0f3fa;
        --gray:#8088ad;
        --base: white;
        --textLight: rgb(90, 88, 88);
        --textCard: black;
        --switchSpinner: white;
        --switch: #5f616d;
        --title-number: black;
    
}

.wrapper{
    padding: 0 30px;
}
.grid{
    display: grid;
    grid-row-gap: 25px;
}

@media screen and (min-width: 400px){

    .grid{
        grid-template-columns: 1fr ;
        grid-column-gap:25px;
    }
  
    }
@media screen and (min-width: 480px){
body{
}
.grid{
    grid-template-columns: 1fr ;
    grid-column-gap:25px;
}
.wrapper{
    max-width: 1080px;
    margin:auto;
}
}

@media screen and (min-width: 558px){
  
    .grid{
        grid-template-columns: 1fr 1fr ;
        grid-column-gap:25px;
    }
 
}
@media screen and (min-width: 924px){
  
    .grid{
        grid-template-columns: 1fr 1fr 1fr;
        grid-column-gap:25px;
    }
 
}

@media screen and (min-width: 1024px){
  
    .grid{
        grid-template-columns: 1fr 1fr 1fr 1fr;
        grid-column-gap:25px;
    }
 
}